import React, { useState } from "react";
import axios from "axios";
import "./AgregarNegocio.css";

const AgregarNegocio = ({ userId }) => {
  const [businessName, setBusinessName] = useState("");
  const [businessAddress, setBusinessAddress] = useState("");
  const [businessPhoneNumber, setBusinessPhoneNumber] = useState("");
  const [businessStatus, setBusinessStatus] = useState(1); // Por defecto activo
  const [businessLogo, setBusinessLogo] = useState(null);
  const [businessLatitude, setBusinessLatitude] = useState("");
  const [businessLongitude, setBusinessLongitude] = useState("");
  const [businessCategorization, setBusinessCategorization] = useState(1); // Ejemplo: Categoría fija por defecto

  const handleImageUpload = (event) => {
    setBusinessLogo(event.target.files[0]);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validación básica
    if (
      !businessName ||
      !businessAddress ||
      !businessPhoneNumber ||
      !businessLatitude ||
      !businessLongitude
    ) {
      alert("Por favor completa todos los campos requeridos.");
      return;
    }

    // FormData para incluir el archivo
    const formData = new FormData();
    formData.append("businessName", businessName);
    formData.append("businessAddress", businessAddress);
    formData.append("businessPhoneNumber", businessPhoneNumber);
    formData.append("businessStatus", businessStatus);
    if (businessLogo) formData.append("file", businessLogo); // Archivo para el logo
    formData.append("businessLatitude", businessLatitude);
    formData.append("businessLongitude", businessLongitude);
    formData.append("businessCategorization", businessCategorization);
    formData.append("business_AreaId", 1); // Área fija de restaurantes
    formData.append("business_UserId", userId); // ID del usuario pasado como prop

    try {
      const response = await axios.post(
        "https://api-degusto.onrender.com/api/business",
        formData,
        {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        }
      );
      alert("Negocio agregado exitosamente.");
      console.log(response.data);
    } catch (error) {
      console.error("Error al agregar negocio:", error);
      alert("Ocurrió un error al intentar agregar el negocio.");
    }
  };

  return (
    <div className="agregar-negocio-container">
      <h1>Registrar Negocio</h1>
      <form onSubmit={handleSubmit}>
        <div className="form-group">
          <label>Nombre del Negocio:</label>
          <input
            type="text"
            value={businessName}
            onChange={(e) => setBusinessName(e.target.value)}
            placeholder="Ingrese el nombre del negocio"
            required
          />
        </div>

        <div className="form-group">
          <label>Dirección:</label>
          <input
            type="text"
            value={businessAddress}
            onChange={(e) => setBusinessAddress(e.target.value)}
            placeholder="Ingrese la dirección"
            required
          />
        </div>

        <div className="form-group">
          <label>Número de Teléfono:</label>
          <input
            type="text"
            value={businessPhoneNumber}
            onChange={(e) => setBusinessPhoneNumber(e.target.value)}
            placeholder="Ingrese el número de teléfono"
            required
          />
        </div>

        <div className="form-group">
          <label>Latitud:</label>
          <input
            type="number"
            step="any"
            value={businessLatitude}
            onChange={(e) => setBusinessLatitude(e.target.value)}
            placeholder="Ingrese la latitud"
            required
          />
        </div>

        <div className="form-group">
          <label>Longitud:</label>
          <input
            type="number"
            step="any"
            value={businessLongitude}
            onChange={(e) => setBusinessLongitude(e.target.value)}
            placeholder="Ingrese la longitud"
            required
          />
        </div>

        <div className="form-group">
          <label>Logo del Negocio:</label>
          <input
            type="file"
            accept="image/*"
            onChange={handleImageUpload}
          />
        </div>

        <div className="form-group">
          <label>Estado del Negocio:</label>
          <select
            value={businessStatus}
            onChange={(e) => setBusinessStatus(e.target.value)}
          >
            <option value={1}>Activo</option>
            <option value={0}>Inactivo</option>
          </select>
        </div>

        <button type="submit" className="submit-button">
          Registrar Negocio
        </button>
      </form>
    </div>
  );
};

export default AgregarNegocio;